.done, .show {
  background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
  border: 0;
  border-radius: 8px;
  box-shadow: 0 3px 5px 2px rgba(255, 105, 135, .3);
  color: white;
  font-size: 16px;
  margin: auto 8px;
  padding: 8px 16px;
  position: absolute;
}

.hide {
  display: none
}

.done {
  background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
  color: #000;
  text-decoration: line-through;
}